.mainContents.home{
    align-items: center;
    margin: 24px;
}
.terminal {
    border-radius: 12px;
    max-width: 1024px;
    width: calc(100% - 32px);
    min-height: 48px;
    display: flex;
    border: 0;
    flex-direction: row;
    padding-left: 24px;
    align-items: center;
    box-shadow: black 0 2px 6px 1px;
    transition-duration: 0.4s;
}
.terminal.dark {
    color: white;
    background: #564043;
}
.terminal.light {
    color: black;
    background: #E1FFE1;
}
.terminal.input:hover{
    cursor: text;
}
.terminal>input {
    padding-top: 12px;
    padding-bottom: 12px;
    border-radius: 12px;
    background: none;
    border: none;
    width: 100%;
    font-size: 16px;
    outline: 0;
}
.terminal.response {
    padding-top: 12px;
    padding-bottom: 12px;
    min-height: 24px;
}
.terminal.input::before{
    content: "$";
    padding-right: 4px;
}
.terminal.input.dark::before{
    color: yellow;
}
.terminal.input.light::before{
    color: blue;
}
